.modal {
  position: relative;
  width: 60%;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 1000;
  background-color: rgb(255, 255, 255);
  border-radius: 30px;
  display: block;
}
.modal img,
.modal p {
  width: 100%;
  padding: 20px;
}

.button {
  display: flex;
  justify-content: flex-end;
  margin-right: 20px;
}
.button button {
  background-color: white;
  border: white;
  font-size: large;
  margin-top: 10px;
  color: black;
}
.button button:hover {
  cursor: pointer;
}
.content {
  margin-left: 1rem;
  margin-bottom: 10px;
}
@media screen and (min-width: 640px) {
  .modal {
    width: 500px;
  }
  .modal img,
  .modal p {
    width: 300px;
  }
}
