@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@400;700&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-family: "Noto Sans JP", sans-serif;
}

body {
  width: 100%;
  background-image: url(/movies/static/media/movie.b24a0e09.jpg);

  background-size: 100%, 100%;
}

.container {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  margin: 1em, auto;
  padding-top: 2em;
}
.container h1 {
  width: 100%;
  height: auto;
  color: white;
  background-color: red;
}
.grid {
  width: 900px;
  height: auto;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 0.4em;
  justify-content: center;
}
.movie {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: rgb(61, 68, 68);
  margin-bottom: 20px;
  padding-bottom: 10px;
}
.movie .menu-movie {
  width: 90%;
  display: flex;
  justify-content: space-evenly;
}
.movie .large {
  display: block;
  width: 60%;
  height: 3em;
  background-color: black;
  color: white;
  border-radius: 10px;
  margin-top: 4px;
}
.movie .large::after {
  content: "About";
  font-size: 1em;
}
.movie .large:hover {
  background-color: bisque;
  color: tomato;
  cursor: pointer;
  border: bisque;
}
.menu-movie .favorite {
  width: 20%;
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
}
.menu-movie .favorite:hover {
  cursor: pointer;
}
.menu-movie .green {
  color: rgb(241, 226, 15);
}
header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  height: 2.3em;
  background-color: rgb(0, 0, 0);
  padding-left: 2em;
  padding-right: 2em;
}
.search input {
  height: 2em;
  border-radius: 5px;
  padding: 2px;
  margin-right: 3px;
}
.search button {
  height: 2em;
  border-radius: 5px;
  background-color: black;
  color: white;
  border: black;
}
.search button:hover {
  cursor: pointer;
  background-color: bisque;
  color: black;
}
.logo {
  font-size: larger;
  color: white;
}
.logo:hover {
  cursor: pointer;
  color: red;
}
.menu {
  display: flex;
}
@media screen and (max-width: 400px) {
  header {
    display: block;
    height: 4em;
  }
  .search {
    width: 100%;
    display: flex;
    justify-content: flex-end;
  }
  .movie .large::after {
    content: "+";
    font-size: 1.5em;
    color: white;
  }
}

.MovieImage_movie__1y_N9 {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  border-radius: 5px;
}
.MovieImage_movie__1y_N9 img {
  width: 100%;
  padding: 0.5em;
}
@media screen and (max-width: 480px) {
  .MovieImage_movie__1y_N9 img {
    padding: 0.1em;
  }
}

